a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

fieldset, img {
  margin: 0;
  padding: 0;
  vertical-align: baseline; }

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

a img, fieldset, img {
  border: none; }

input {
  &[type=email], &[type=tel], &[type=text] {
    -webkit-appearance: none; } }

textarea {
  -webkit-appearance: none; }

button, input[type=submit] {
  cursor: pointer; }

button::-moz-focus-inner, input[type=submit]::-moz-focus-inner {
  padding: 0;
  border: 0; }

textarea {
  overflow: auto; }

button, input {
  margin: 0;
  padding: 0;
  border: 0; }

a {
  outline: none;

  &:focus {
    outline: none; } }

button, div, h1, h2, h3, h4, h5, h6, input, select, span, textarea {
  outline: none; }

ol, ul {
  list-style-type: none; }

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%; }

*, :after, :before {
  box-sizing: inherit; }

@font-face {
  font-family: "gBlack";
  src: url(../fonts/gotham/GothamBlack.woff2) format("woff2"), url(../fonts/GothamBlack.woff) format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "gBook";
  src: url(../fonts/gotham/GothamBook.woff2) format("woff2"), url(../fonts/GothamBook.woff) format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "gBold";
  src: url(../fonts/gotham/GothamBold.woff2) format("woff2"), url(../fonts/GothamBold.woff) format("woff");
  font-weight: 400;
  font-style: normal; }

body {
	font-family: gBook, sans-serif;
	padding: 50px;
	background: #6e4567;
	line-height: 1.4; }

.container {
  max-width: 1000px;
  margin: 0 auto; }


.wrapper__box {
	position: relative;
	width: 100%;
	height: 400px;
	margin-bottom: 50px;
	border-top-left-radius: 50px;
	border-bottom-right-radius: 50px;
	background: #6e4567;
	box-shadow:  20px 20px 60px #5e3b58, -20px -20px 60px #7f4f76;
	overflow: hidden;
	@include trs;
	.row {
		display: flex;
		flex-direction: column; } }

.wrapper__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; }
